/**
 * Home page scripts.
 *
 * @module Home
 */

import { $scrolledElements } from "../modules/dev/_helpers";
import { Resp } from "../modules/dev/_helpers";
import gsap from "../modules/dev/gsap.min";
import { CSSRulePlugin } from "../modules/dev/CSSRulePlugin.min";
import { MorphSVGPlugin } from "../modules/dev/MorphSVGPlugin.min";
import { CustomEase } from "../modules/dev/CustomEase.min";
import Swiper from "../modules/dev/swiper.min";
import ScrollMagic from "../modules/dev/ScrollMagic.min";

export default class Home {
  /**
   * Cache data, make preparations and initialize page scripts.
   */
  constructor() {
    this.init();
  }

  initPreloader(preloaderElement) {
    let preloaderEl = document.querySelector(preloaderElement);
    $scrolledElements.addClass('active');
    window.addEventListener("load", function() {
      preloaderEl.classList.add("hidden");
      setTimeout(() => {
        window.requestAnimationFrame(() => {
          preloaderEl.parentNode.removeChild(preloaderEl);
          $scrolledElements.removeClass('active');
        });
      }, 500);
    });
  }
  initModal(modalTrigger) {
    modalTrigger.click(function(){
      var modalTriggerDataAttr = $(this).attr('data-modal');
      $('#modal-container').removeAttr('class').addClass(modalTriggerDataAttr);
      $scrolledElements.addClass('active');
    })
    
    $('#modal-container, .js-close-modal').click(function(e){
      const { target } = e;
      if (!$(target).closest(".modal").length || $(target).hasClass('js-close-modal')) {
        $(this).addClass('out');
        $scrolledElements.removeClass('active');
      }
    });
  }
  initHeaderActions() {
    let currentScrollTop =
        window.pageYOffset || document.documentElement.scrollTop,
      isVisible = true;
    const headerTween = gsap
      .to(".header", 0.5, { y: "-100%", ease: Power1.easeInOut })
      .reverse()
      .pause();
    const show = () => {
      if (!isVisible) {
        headerTween.reverse();
        isVisible = true;
      }
    };
    const hide = () => {
      if (isVisible) {
        headerTween.play();
        isVisible = false;
      }
    };

    if (Resp.isMobile) {
      let cEase = CustomEase.create(
        "custom",
        "M0,0,C0.033,0,0.026,0.204,0.146,0.216,0.294,0.23,0.294,-0.178,0.464,-0.178,0.616,-0.178,0.832,0.19,1,1"
      );
      var burgertl = gsap.timeline({ paused: true, reversed: true })
        .set('.mobile-menu-info li',{x:-300})
        .to(
          ".stage-one",
          0.4,
          { morphSVG: ".middle-one", ease: cEase },
          "start"
        )
        .to(
          ".stage-two",
          0.4,
          { morphSVG: ".middle-two", ease: cEase },
          "start"
        )
        .to(
          ".stage-line",
          0.4,
          { scaleX: 0, transformOrigin: "50% 50%", ease: Back.easeIn },
          "start"
        )
        .to(".stage-one", 0.1, { morphSVG: ".end-one" }, "end")
        .to(".stage-two", 0.1, { morphSVG: ".end-two" }, "end")
        .from(".mobile-menu-info", 0.4, {
          height: 0
        })
        .to('.mobile-menu-info li', .15,{x:0,ease: Sine.easeOut, stagger: .1})
        .to('.mobile-menu-info li', .2,{marginBottom:'15px', ease: Power1.easeOut});
      var isReversed = () => {
        if (burgertl.reversed()) {
          burgertl.play();
          $scrolledElements.addClass("active");
        } else {
          burgertl.reverse();
          $scrolledElements.removeClass("active");
        }
      };

      let time;
      document
        .querySelector(".mobile-menu")
        .addEventListener("click", function() {
          if (time) {
            clearTimeout(time);
          }
          time = setTimeout(() => isReversed(), 500);
        });
    }
    // go to anchors
    $("[data-scroll]").click(function(e) {
      e.preventDefault();
      const target = "#" + $(this).data("scroll");
      const $target = $(target);
      if (!(typeof burgertl === "undefined" || burgertl === null) && !burgertl.reversed()) {
        isReversed();
      }
      const scrollTo = () => {
        $scrolledElements.animate(
          {
            scrollTop: $target.offset().top
          },
          1000
        );
      };
      if (Resp.isMobile) {
        setTimeout(() => {
          scrollTo();
        }, 1000);
        return false;
      } else {
        scrollTo();
      }
    });
    // toggle header visibility on scroll
    const refresh = () => {
      let newScrollTop =
        window.pageYOffset || document.documentElement.scrollTop;
      if (newScrollTop > 0 && newScrollTop > currentScrollTop) {
        hide();
        if (
          !(typeof burgertl === "undefined" || burgertl === null) &&
          !burgertl.reversed()
        ) {
          burgertl.reverse();
        }
      } else if (newScrollTop < currentScrollTop) {
        show();
      }
      currentScrollTop = newScrollTop;
    };
    window.addEventListener("scroll", refresh, {
      passive: true
    });
  }
  initOnEnterAboutCounters() {
    const numberWithCommas = n => {
      const parts = n.toString().split(".");
      return parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    };

    const endingCounterVar = [600, 4, 800];

    for (let i = 0; i <= 2; i++) {
      let counter = { var: 0 };
      let tal = document.getElementById("tal-" + i);
      const aboutCounterController = new ScrollMagic.Controller();
      const aboutCounterTween = gsap.timeline()
      .from(tal, { opacity: 0})
      .to(counter, 2, {
        var: endingCounterVar[i],
        onUpdate: function() {
          let nwc = numberWithCommas(counter.var);
          tal.innerHTML = nwc;
        },
        ease: Circ.easeOut
      });
      aboutCounterTween.pause();
      new ScrollMagic.Scene({
        triggerElement: ".about__wrapper",
        offset: -50
      })
        .on("enter", () => {
          aboutCounterTween.play();
        })
        .addTo(aboutCounterController);
    }
  }
  initElemAppearence() {
    const elemController = new ScrollMagic.Controller();
    $('.animate-el').each(function(){
      const elemAppearanceTween = gsap
        .timeline()
        .from(this, { opacity: 0, duration: 0.8, y: 50, stagger: 0.2 });
      elemAppearanceTween.pause();
      new ScrollMagic.Scene({
        triggerElement: this,
        offset: -150
      })
      .on("enter", () => {
        elemAppearanceTween.play();
      })
      .addTo(elemController);
    })
  }
  initAccordAppearence() {
    const accordController = new ScrollMagic.Controller();
    const rule = CSSRulePlugin.getRule(".accord:after");
    let elemAppearanceTween = gsap.timeline({ defaults: { duration: 0.6 } });
    elemAppearanceTween
      .from(".animate-el-1", { opacity: 0, y: 50, stagger: 0.6 })
      .to(rule, { cssRule: { scaleY: 0 } }, "-=1.2");
    elemAppearanceTween.pause();
    new ScrollMagic.Scene({
      triggerElement: "#gallery",
      offset: 150
    })
      .on("enter", () => {
        elemAppearanceTween.play();
      })
      .addTo(accordController);
  }
  
  initOnMouseMoveParallax() {
    $("#gallery").mousemove(function(e) {
      parallaxIt(e, ".gallery__wrapper-left--img", -100);
      parallaxIt(e, ".center-img", 100);
      parallaxIt(e, ".gallery__wrapper-right--img", -100);
      parallaxIt(e, ".gallery__wrapper-desc", -30);
    });

    function parallaxIt(e, target, movement) {
      var $this = $("#gallery");
      var relX = e.pageX - $this.offset().left;
      var relY = e.pageY - $this.offset().top;

      gsap.to(target, 3, {
        x: ((relX - $this.width() / 2) / $this.width()) * movement,
        y: ((relY - $this.height() / 2) / $this.height()) * movement
      });
    }
  }
  initSlider(el, opts) {
    new Swiper(el, opts);
  }
  initScrollToTop(anyScrollButton) {
    $(window).scroll(function() {
      if ($(window).scrollTop() > 2400 || $(window).scrollTop() > 2000 && Resp.isSmallMobile) {
        anyScrollButton.addClass("show");
      } else {
        anyScrollButton.removeClass("show");
      }
    });
    anyScrollButton.on("click", function(e) {
      e.preventDefault();
      $scrolledElements.animate({ scrollTop: 0 }, 1200);
    });
  }
  /**
   * Initialize Home page scripts.
   */
  init() {
    this.initPreloader(".preloader");
    this.initHeaderActions();
    this.initModal($('.header__wrapper__callback'));
    this.initElemAppearence();
    this.initAccordAppearence();
    this.initSlider(".first-screen__container", {
      speed: 1000,
      parallax: true,
      autoplay: true,
      pagination: {
        el: ".first-screen__controls-pagination"
      },
      navigation: {
        nextEl: ".first-screen__controls-next",
        prevEl: ".first-screen__controls-prev"
      }
    });
    if(Resp.isMobile) {
      this.initSlider(".gallery-slider", {
        // Enable lazy loading
        lazy: true,
        pagination: {
          el: '.gallery-slider-pagination',
          clickable: true,
        },
        navigation: {
          nextEl: '.gallery-slider-next',
          prevEl: '.gallery-slider-prev',
        },
      });
    }
    this.initSlider(".planning-container", {
      speed: 600,
      parallax: true,
      effect: "fade",
      pagination: {
        el: ".planning__controls-pagination"
      },
      navigation: {
        nextEl: ".planning__controls-next",
        prevEl: ".planning__controls-prev"
      }
    });
    this.initOnEnterAboutCounters();
    if(!Resp.isMobile) {
      this.initOnMouseMoveParallax();
    }
    this.initScrollToTop($(".scroll-to-top-button"));
  }
}



